

import axios from 'axios';
import store from '@/store';
import EventEmitter from 'events';
const qs = require('qs');
 

class CoreAPI extends EventEmitter{
    constructor(){
        super();
        this.host = "https://thistitle.ru/api/main/api.php";
    }
    get(data){
        data.addToURl = (data.addToURl) ? data.addToURl : ''
        return new Promise((resolve)=>{
            axios.get(`${this.host}?action=${data.action}${data.addToURl}`,{
                method : "GET",
            }).then((res)=>{
                resolve(res.data);
            })
        })
    }
    post(data){
        data = qs.stringify(data);

        return new Promise((resolve)=>{
            axios.post(this.host,data).then((res)=>{
                resolve(res.data);
            })
        })
    }


     /*
        d0.0.1
    */
    async getProducts(filter = ""){
        return this.get({action : "get_products", addToURl : filter})
    }
    /*
        d0.0.2
    */
    async checkAuthorizeStatus(){
        let ref = this;
        return new Promise((resolve)=>{
            store.state.token = (localStorage.getItem("token")) ? localStorage.getItem("token") : 'not';
            store.state.user = false;
            ref.get({
                action : "check_token",
                addToURl : `&token=${store.state.token}`
            }).then((result)=>{
                if(result.status == "ok"){
                    store.state.user = result.user;
                    resolve(true)
                }
                else{
                    resolve(false)
                }
            })
        })
    }

    async login(login,password){
        let ref = this;
        return new Promise((resolve)=>{
            ref.post({action: "get_token",login: login, password : password}).then((result)=>{
                if(result.status == "ok"){
                    localStorage.setItem("token",result.token)
                    store.state.token = result.token;
                    resolve(true);  
                }
            })
        })
    }

    /*
        d0.0.3
    */
    async getCart(){
        let ref = this;
        return new Promise((resolve)=>{
            ref.get({
                action : "cart_get",
                addToURl : `&user_token=${store.state.user.token}`
            }).then((result)=>{
                store.state.cart = result.cart;
                resolve(true);
            });
        })
    }
    async addToCart(prod_id){
        return new Promise((resolve)=>{
            this.post({action: "cart_add",prod_id: prod_id, user_token : store.state.user.token}).then((result)=>{
                console.log(result);
                if(result.status == "ok"){
                    resolve(true);  
                }
                this.getCart();
                
            })
        })
    }
    async removeFromCart(prod_id){
        return new Promise((resolve)=>{
            this.post({action: "cart_remove",prod_id: prod_id, user_token : store.state.user.token}).then((result)=>{
                if(result.status == "ok"){
                    resolve(true);  
                }
                this.getCart();
            })
        })
    }

    checkInCart(prod_id){
        if(store.state.cart){
            for(let i of store.state.cart){
                if(i.prod_id == prod_id){
                    return i;
                }
            }
        }
        
        return false;
    }


    /*

    d0.0.7

    */

    devAuth(){
        this.login("test","test").then((authorized)=>{
            if(authorized){
                this.checkAuthorizeStatus()
            }
            else{
                console.log("Неверн логин или пароль");
            }
        })
    }

    /*
    d0.0.8
    */


    client_getCart(){
        if(!localStorage.getItem("cart")){
            localStorage.setItem("cart",JSON.stringify([]));
        }
        let cart = JSON.parse(localStorage.getItem("cart"));
        return cart;
    }

    client_addToCart(product,size){
        if(this.client_getFromCart(product.id)){
            this.client_updateProdAmount(product.id,1);
        }
        else{
            let cart = this.client_getCart();
            product["amount"] = 1;
            product["size"] = size;
            cart.push(product);
            localStorage.setItem("cart",JSON.stringify(cart));
        }
        this.emit("cart-updated");
    }

    client_updateProdAmount(id,size,amount){
        let cart = this.client_getCart();
        for(let product in cart){
            if(~~cart[product].id == ~~id && cart[product].size == size){
                cart[product].amount += amount;

                if(cart[product].amount < 1){
                    cart.splice(cart.indexOf(cart[product]),1);
                }
            }
        }
        
        localStorage.setItem("cart",JSON.stringify(cart));
        this.emit("cart-updated");
    }

    client_getFromCart(id,sizeID){
        let cart = this.client_getCart();
        for(let product in cart){
            if(~~cart[product].id == ~~id && cart[product].size == sizeID) return cart[product];
        }
        return false;
        
    }

    client_clearCart(){
        localStorage.setItem("cart",JSON.stringify([]));
        this.emit("cart-updated");
    }
    client_removeFromCart(product){
        let cart = this.client_getCart();
        cart.splice(cart.indexOf(cart[product]),1);
        localStorage.setItem("cart",JSON.stringify(cart));
        this.emit("cart-updated");
    }

    client_getTotalAmounts(){
        let amount = 0
        let cart = this.client_getCart();
        for(let elem of cart){
            amount += elem.amount;
        }
        return amount
    }

    
    create_order(data){
        data = qs.stringify(data);

        return new Promise((resolve)=>{
            axios.post(`https://thistitle.ru/payment/new.php`,data).then((res)=>{
                resolve(res.data);
            })
        })
    }

    getCategories(){
        return this.get({
            action: "get_categories"
        })
    }


}
export default new CoreAPI();
