 class Helper{
    constructor(){
        this.metas = {
            "material" : "Материал"
        }
    }

    findSizeNameByID(product,sizeid){
        for(let sizes of product.sizes){
            if(sizes.id == sizeid) return sizes.sizename;
        }
    }

    translateMetaKey(key){
        return this.metas[key];
    }
    getImageSrc(product,type){
        let images = product.images;
        if(images){
            for(let i of images){
                if(i.type == type){
                    return i.path;
                }
            }
        }
        else{
            return '@/assets/icons/product_none.svg';
        }
    }
}
export default new Helper();
