<template>
    <footer>
        <div class="release-note">Latest dev-release: {{ latest }}</div>
        <div class="release-note">Current version: {{ $store.state.currentVersion }}</div>
    </footer>
</template>

<script>
    export default {
        name: 'FooterVue',
        data(){
            return {
                latest : ""
            }
        },
        mounted(){
            fetch("https://thistitle.ru/api/main/api.php?action=get_dev_data").then(async (result)=>{
            let data = await result.json()
                
            this.latest = data.data.version;
            })
        }
    }
    
</script>

<style lang="scss" scoped>
    footer{
        padding: 10px;
    }
    .release-note{
        color: #cacaca;
        user-select: none;
    }
</style>